import React, { Component } from 'react';

import { FormGroup,FormControl,ControlLabel } from 'react-bootstrap';

class FormInstance2 extends Component {
  constructor() {
    super();
    this.state = {
        password: null,
    };

    this.handlePasswordChange = this.handlePasswordChange.bind(this);

    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleKeyPress(event) {
    if(event.key === 'Enter' && this.state.password){
        document.getElementById("change-my-password").click();
        //this.setState({password:''});
    }
  }

  handlePasswordChange(event) {
     this.setState({password: event.target.value});
  }

  dummy(e) {
    e.preventDefault();
  }

  render() {
    var changePassword = this.dummy;
    if (this.state.password) {
        changePassword = this.props.changePassword.bind(this.props.retThis,this.state.password);
    }
    return (
   <form onSubmit={e => (e.preventDefault())}>
    <div id="login-form" style={{"background":"#fff","opacity":"0.7","marginTop":"100px","marginBottom":"100px","padding":"0 0","fontFamily":"Roboto,Lucida Grande,DejaVu Sans,Bitstream Vera Sans,Verdana,Arial,sans-serif"}}>
    <div style={{ "padding":"0 20px","marginTop":"0px"}}>
		<FormGroup controlId="formHorizontalPassword">
			<ControlLabel>Enter new password</ControlLabel>
			<FormControl type="password" onChange={this.handlePasswordChange} onKeyPress={this.handleKeyPress} />
		</FormGroup>
        <section className="page-header" style={{"padding":"0"}}>
            <a style={{"width":"100%","height":"100%","margin":"0"}} onClick={changePassword} className="btn" id="change-my-password" href="/change-my-password/">Save</a>
        </section>
	</div>
  </div>
  </form>
    );
  }
}

export default FormInstance2;



