import React, { Component } from 'react';

import { FormGroup,FormControl,ControlLabel } from 'react-bootstrap';

class FormInstance extends Component {
  constructor() {
    super();
    this.state = {
        username: null,
    };

    this.handleUsernameChange = this.handleUsernameChange.bind(this);

    this.handleKeyPress = this.handleKeyPress.bind(this);
  }

  handleKeyPress(event) {
    if(event.key === 'Enter' && this.state.username){
        document.getElementById("send-reset-email").click();
        //this.setState({password:''});
    }
  }

  handleUsernameChange(event) {
     this.setState({username: event.target.value});
  }

  componentDidMount() {
  }

  dummy(e) {
    e.preventDefault();
  }

  render() {
    var sendEmail = this.dummy;
    if (this.state.username) {
        sendEmail = this.props.sendEmail.bind(this.props.retThis,this.state.username);
    }
    return (
   <form onSubmit={e => (e.preventDefault())}>
    <div id="login-form" style={{"background":"#fff","opacity":"0.7","marginTop":"100px","marginBottom":"100px","padding":"0 0","fontFamily":"Roboto,Lucida Grande,DejaVu Sans,Bitstream Vera Sans,Verdana,Arial,sans-serif"}}>
    <div style={{ "padding":"0 20px","marginTop":"0px"}}>
		<FormGroup controlId="formHorizontalUsername">
			<ControlLabel>Enter username or email</ControlLabel>
			<FormControl type="text" autoComplete="off" onChange={this.handleUsernameChange} onKeyPress={this.handleKeyPress} spellCheck="false"/>
		</FormGroup>
        <section className="page-header" style={{"padding":"0"}}>
            <a style={{"width":"100%","height":"100%","margin":"0"}} onClick={sendEmail} className="btn" id="send-reset-email" href="/send-reset-email/">Send Email</a>
        </section>
	</div>
  </div>
  </form>
    );
  }
}

export default FormInstance;



