import React, { Component } from 'react';
import MainNav from './MainNav';

import FormInstance from './FormInstance'; // get username, send email
import FormInstance2 from './FormInstance2'; // change password

import AppLayout from './AppLayout';

function getParameterByName(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, "\\$&");
    var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
}


class App extends Component {

  constructor() {
    super();
    this.state = {sessionId:'',content:null,auth:false,
                ip:'guest',trusted:0,validated:false,resetKey:'',username:''};
  }

  loadPage() {
        document.getElementById("App").style.visibility = "visible";

        const script = document.createElement("script");

        script.src = "//platform.twitter.com/widgets.js";
        script.async = true;

        document.body.appendChild(script);

        if (this.state.username === '') {
            document.getElementById("formHorizontalUsername").focus();
        }
  }

  isAuth() {
    fetch('https://bw3.bitweevil.com/is-auth.php', {
      credentials: 'include',
      mode: 'cors',
      headers: {
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            window.location.assign("https://anothergithubber.github.io/ubiquitous-broccoli/");
        } else {
            var key = getParameterByName('key');
            if (key) {
                var username = getParameterByName('username');
                if (username) {
                    this.checkKey(key, username);
                }
            } else {
                var content = <div style={{"padding":"0px"}}><FormInstance sendEmail={this.sendEmail} retThis={this}/></div>;
                this.setState({content:<AppLayout id="login-component" title={"Login"} content={content} />,auth:true,sessionId:res.sessionId});
                this.loadPage();
            }
        }
    }.bind(this));
  }

  checkKey(key, username) {
    fetch('https://bw3.bitweevil.com/check-reset-key.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'post',
      body: JSON.stringify({
          'sessionId': this.state.sessionId,
          'ip': this.state.ip,
          'reset_key': key,
          'username': username,
      }),
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
//            alert('Key ok');
            this.setState({resetKey:res.resetKey,username:username});
            var content = <div style={{"padding":"0px"}}><FormInstance2 changePassword={this.resetPassword} retThis={this}/></div>;
            this.setState({content:<AppLayout id="login-component" title={"Login"} content={content} />,auth:true,sessionId:res.sessionId});
            this.loadPage();
            document.getElementById("formHorizontalPassword").focus();
            // Show password reset
            //window.location.assign("https://anothergithubber.github.io/ubiquitous-broccoli/");
        } else {
//            console.log(res.reason);
 //           console.log('Key bad');
        }
    }.bind(this));
  }

  resetPassword(password, e) {
    e.preventDefault();
    fetch('https://bw3.bitweevil.com/reset-my-password.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'post',
      body: JSON.stringify({
          'password': password,
          'username': this.state.username,
          'reset_key': this.state.resetKey,
          'sessionId': this.state.sessionId,
          'ip': this.state.ip,
      }),
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        if (res.valid) {
            alert('Password reset');
//            window.location.assign("https://anothergithubber.github.io/ubiquitous-broccoli/");
        } else {
            console.log(res.reason);
            document.getElementById("formHorizontalPassword").value = '';
            document.getElementById("formHorizontalPassword").focus();
        }

//        document.getElementById("formHorizontalPassword").value = '';
//        document.getElementById("formHorizontalPassword").focus();
    });
  }

  getRemoteAddress() {
    fetch('https://bw3.bitweevil.com/get-remote-address.php', {
      credentials: 'include',
      headers: {
          'Content-Type':'application/x-www-form-urlencoded',
          'Accept': 'text/html',
      },
    })
    .then(function(res) {
        return res.json();
    }).then(function(res) {
        var trusted = 0;
        if (res.status === 'trusted') {
            trusted = 1;
        }
        this.setState({ ip: res.ip, trusted: trusted });
        this.isAuth();
    }.bind(this));
  }

  componentDidMount() {
    this.getRemoteAddress();
    var date = "Thu Jul  6 09:38:24 UTC 2017";
    console.log('date: '+date);
  }

  sendEmail(username, e) {
    e.preventDefault();
    fetch('https://bw3.bitweevil.com/send-reset-email.php', {
      credentials: 'include',
      mode: 'cors',
      method: 'post',
      body: JSON.stringify({
          'sessionId': this.state.sessionId,
          'ip': this.state.ip,
          'username': username,
      }),
      headers: {
      'Content-Type':'application/x-www-form-urlencoded',
      'Accept': 'text/html',
      },
    })
    .then(function(res) {
//        console.log('raw res: '+res.toString());
        return res.json();
    }).then(function(res) {
        if (res.valid) {
//            alert('Reset email sent');
            this.setState({content:<section className="main-content">If there is an account associated to the username or email you entered, an email will have been sent with a link to reset the password.</section>});
        } else {
//            console.log(res.reason);
        }
    }.bind(this));
  }

  description() {
    return (
        <section className="main-content">
       <div style={{"clear":"both","overflow":"hidden","width":"100%"}}>
           <div style={{"float":"left","display":"inline-block","width":"100%"}}>
            <span style={{"fontSize":"11px"}}>{this.state.validated ? null : "An email will be sent to the address specified on your account"} <i>{this.state.validated ? null : "You might have to check your spam box."}</i></span>
           </div>
       </div>
        <div>
        {this.state.validated ? null : this.state.content}
        </div>
        </section>
    );
  }

  render() {
    var content = null;
    if (this.state.content && this.state.auth) {
        content = this.description();
    } else if (this.state.validated) {
        content = <section className="main-content">Your email has been validated.</section>;
    } else {
        content = this.state.content;
    }
    return (
      <div id="App" style={{"visibility":"hidden"}}>
       <MainNav />
        {content}
        <section className="main-content">

            <span>
                <a href="https://twitter.com/anothergithubbe" data-show-screen-name="false" className="twitter-follow-button" id="twitter-follow-button" style={{"verticalAlign":"middle !important","display":"none"}} data-show-count="false">Follow</a>
            </span>

                <span className="site-footer-social">
                    <div className="fb-like" style={{"verticalAlign":"top","zoom":"1","*display":"inline"}} data-href="https://anothergithubber.github.io/probable-chainsaw/" data-layout="button" data-action="like" data-size="small" data-show-faces="false" data-share="true"></div>
                </span>

          <footer className="site-footer">
            <span className="site-footer-credits"><a href="https://github.com/anothergithubber/anothergithubber.github.io">probable-chainsaw</a> by <a href="https://github.com/anothergithubber">anothergithubber</a>.</span>
          </footer>
        </section>
      </div>
    );
  }
}

export default App;



